.titulo-head {
  padding-top: 3rem;
}

html {
  font-size: 62.5%;
  box-sizing: border-box;
  min-height: 100%;
}
html h1, html h2, html h3, html p, html a {
  font-family: "Dosis", sans-serif;
  color: white;
}

*, *:before, *:after {
  box-sizing: inherit;
}

img {
  width: 100%;
  padding: 0;
}

.contenido {
  max-width: 100rem;
  width: 96%;
  margin: 0 auto;
}

.contenido2 {
  max-width: 110rem;
  width: 96%;
  margin: 0 auto;
}

.titulo-head {
  text-align: center;
  color: #222020;
  text-transform: uppercase;
  font-size: 3.5rem;
}

.contenido-header, .footer {
  background-color: #20633c;
  height: auto;
}
.contenido-header .logo-header p, .footer .logo-header p {
  font-size: 1.5rem;
  color: #20633c;
  text-transform: uppercase;
  font-weight: bold;
  border: 2px solid #20633c;
  background-color: white;
  padding: 0.5rem 0.5rem;
}
@media (min-width: 768px) {
  .contenido-header .logo-header p, .footer .logo-header p {
    font-size: 2rem;
    padding: 0.8rem 1.5rem;
  }
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 3rem;
}

.navegacion-header nav {
  display: none;
}
@media (min-width: 768px) {
  .navegacion-header nav {
    display: block;
  }
}

.nav {
  color: black;
  font-weight: bold;
  margin-right: 2rem;
  font-size: 1.7rem;
  color: white;
  text-transform: uppercase;
  transition: all 0.3s ease-in;
  text-decoration: none;
  position: relative;
  padding-bottom: 1rem;
}
.nav:before {
  content: "";
  display: block;
  margin: 0;
  width: 0;
  height: 5px;
  background: linear-gradient(to right, white, #20633c);
  position: absolute;
  bottom: 0;
  transition: all 0.3s;
}
.nav:hover:before {
  width: 100%;
}

.nav:before {
  left: 50%;
}
.nav:hover:before {
  left: 0;
}
.nav:hover {
  color: #b4a1a1;
}

.navegacion-mobile img {
  width: 4rem;
  filter: invert(90%);
}
@media (min-width: 768px) {
  .navegacion-mobile {
    display: none;
  }
}

.footer-contenido h1 {
  text-align: center;
  padding-top: 4rem;
  text-transform: uppercase;
}
.footer-contenido .navegacion-footer {
  padding: 3rem 0;
}
.footer-contenido .redes-sociales {
  display: flex;
  justify-content: center;
  margin-top: 4rem;
}
.footer-contenido .redes-sociales img {
  max-width: 3rem;
  margin-right: 2rem;
  filter: invert(100%);
  transition: all 0.3s;
}
.footer-contenido .redes-sociales img:hover {
  filter: invert(60%);
}
.footer-contenido .leyenda {
  padding-bottom: 3rem;
  font-size: 1.4rem;
  text-align: center;
}

.nav-footer {
  display: block;
}

.navegacion-mobile .nav-mobile {
  display: none;
}

.navegacion-mobile:hover .nav-mobile {
  display: block;
  position: absolute;
  max-width: 20rem;
  right: 7px;
  top: 80px;
  background: linear-gradient(to bottom, #20633c, #6C3483);
  height: auto;
  z-index: 7;
  border-radius: 0.5rem;
  -webkit-box-shadow: 0 8px 10px 1px rgba(0, 0, 0, 0.14), 0 3px 14px 2px rgba(0, 0, 0, 0.12), 0 5px 5px -3px rgba(0, 0, 0, 0.3);
  box-shadow: 0 8px 10px 1px rgba(0, 0, 0, 0.14), 0 14px 14px 13px rgba(0, 0, 0, 0.12), 0 14px 14px 7px rgba(126, 58, 58, 0.3);
}
.navegacion-mobile:hover .nav-mobile nav {
  display: flex;
  flex-direction: column;
  padding: 2rem;
}
.navegacion-mobile:hover .nav-mobile nav a {
  font-size: 2rem;
  text-align: center;
  text-decoration: none;
  margin-bottom: 1.5rem;
  text-transform: uppercase;
  margin-right: 0;
}

.navegacion-header .nav-footer {
  display: block;
  text-align: center;
}
.navegacion-header .nav-footer a {
  font-size: 1.2rem;
}
@media (min-width: 768px) {
  .navegacion-header .nav-footer a {
    font-size: 2rem;
  }
}

.contenido-nosotros {
  margin-top: 4rem;
  gap: 2rem;
  text-align: start;
}
.contenido-nosotros p {
  font-size: 2rem;
  color: #2b2828;
  line-height: 2.5rem;
  text-transform: initial;
}
@media (min-width: 1024px) {
  .contenido-nosotros {
    margin-top: 8rem;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }
}

.contenido-ruta {
  margin-top: 6rem;
}
.contenido-ruta h1 {
  font-size: 3.5rem;
  color: #2b2828;
}
.contenido-ruta .contenedor-imagen {
  background-image: url(../img/imagesContent/rutaImagen.png);
  width: 100%;
  height: 100vh;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
}
.contenido-ruta .contenedor-imagen .contain {
  background-color: rgba(95, 94, 94, 0.5);
  position: absolute;
  width: 100%;
  height: 100vh;
  margin-top: 0;
}
.contenido-ruta .contenedor-imagen p {
  position: absolute;
  top: 30%;
  font-size: 1.5rem;
}
@media (min-width: 768px) {
  .contenido-ruta .contenedor-imagen p {
    font-size: 3.5rem;
  }
}

.productos-contenido {
  display: grid;
  gap: 1.5rem;
  margin-top: 5rem;
  grid-template-columns: repeat(2, 1fr);
}
@media (min-width: 768px) {
  .productos-contenido {
    grid-template-columns: repeat(3, 1fr);
  }
}
@media (min-width: 1024px) {
  .productos-contenido {
    grid-template-columns: repeat(4, 1fr);
  }
}
.productos-contenido .contenido-target {
  background-color: #e6dddd;
  border-radius: 0.5rem;
  height: auto;
}
.productos-contenido p {
  color: #2b2828;
}
.productos-contenido img {
  height: 25rem;
  object-fit: cover;
  border-top-left-radius: 0.5rem;
  border-top-right-radius: 0.5rem;
}

.info-target {
  padding: 1rem;
}
.info-target h1 {
  color: #2b2828;
  font-size: 2rem;
}
.info-target p {
  font-size: 1.7rem;
  font-weight: 800;
}
.info-target .ahora {
  color: #0f880f;
}
.info-target .antes {
  color: #c01d1d;
}

.disponible {
  display: flex;
  align-items: center;
  gap: 1rem;
}
.disponible img {
  width: 3rem;
  height: 3rem;
}

.ver-mas {
  margin-top: 2rem;
  background-color: #20633c;
  padding: 0.5rem 0;
  transition: all 0.3s;
}
.ver-mas a {
  color: white;
  font-size: 2rem;
  text-decoration: none;
  display: flex;
  justify-content: center;
}
.ver-mas:hover {
  background-color: #1a611a;
}

.target-producto {
  display: flex;
  margin-top: 5rem;
  gap: 4rem;
  flex-direction: column;
}
@media (min-width: 768px) {
  .target-producto {
    flex-direction: row;
  }
}
@media (min-width: 1024px) {
  .target-producto .div-imagen img {
    width: 50rem;
  }
}
.target-producto .div-informacion {
  padding: 1.5rem;
}
.target-producto .div-informacion h1 {
  color: #3d3a3a;
  font-size: 4rem;
}
.target-producto .div-informacion p {
  font-size: 2.5rem;
  font-weight: bold;
  margin: 1rem 0;
}
.target-producto .div-informacion-producto p {
  margin-top: 3rem;
  color: #3d3a3a;
  line-height: 3rem;
}
.target-producto .div-informacion-producto h1 {
  font-size: 2.5rem;
}
.target-producto .div-informacion-producto .contact {
  margin-top: 4rem;
}
.target-producto .div-informacion-producto a {
  text-decoration: none;
  color: #6C3483;
}

.disponible-icon {
  display: flex;
  gap: 2rem;
}
.disponible-icon img {
  width: 3rem;
}
.disponible-icon p {
  color: #3d3a3a;
}

.testimoniales {
  margin-top: 4rem;
}
.testimoniales h1 {
  font-size: 2rem;
  color: #3d3a3a;
}
.testimoniales .formulario {
  max-width: 60rem;
  margin: 3rem auto;
  background: linear-gradient(to bottom, #3c913c, #20633c);
  padding: 2rem;
  border-radius: 0.5rem;
  color: white;
}
.testimoniales .formulario .inputs-form {
  display: flex;
  flex-direction: column;
  margin-bottom: 1.5rem;
}
.testimoniales .formulario .inputs-form label {
  text-align: start;
  margin-bottom: 1rem;
  font-size: 2rem;
}
.testimoniales .formulario .inputs-form input {
  font-size: 2rem;
  padding: 0.5rem;
}
.testimoniales .formulario .btn-enviar {
  font-size: 2.5rem;
  padding: 0.5rem 3rem;
  background: #3c913c;
  border: none;
  color: white;
}
.testimoniales .formulario .btn-enviar:hover {
  cursor: pointer;
}
.testimoniales #mensaje {
  height: 15rem;
  font-size: 1.5rem;
}
.testimoniales p {
  color: white;
}

.div-testimonio-criticas {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 1rem;
  margin-top: 3rem;
}
@media (min-width: 768px) {
  .div-testimonio-criticas {
    grid-template-columns: repeat(3, 1fr);
    gap: 3rem;
  }
}
.div-testimonio-criticas p {
  font-size: 1.5rem;
}
@media (min-width: 768px) {
  .div-testimonio-criticas p {
    font-size: 2rem;
  }
}
.div-testimonio-criticas .footer-testimonio {
  font-size: 1.5rem;
}
.div-testimonio-criticas p, .div-testimonio-criticas .footer-testimonio {
  color: #3d3a3a;
}

.cards {
  border: 2px solid #20633c;
  padding: 2rem 0.5rem;
}
@media (min-width: 768px) {
  .cards {
    padding: 2rem 3rem;
  }
}

.testimoniales-inicio {
  margin: 4rem auto;
}

.div-testimonios {
  font-size: 1.7rem;
  max-width: 60rem;
  margin: 3rem auto;
}
.div-testimonios p {
  background: linear-gradient(to right, #a11a1a, #20633c);
  padding: 1rem 0;
}

.home {
  position: relative;
}

.home .contenido-header {
  background: transparent;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 6;
}

.cortina {
  background-color: rgba(0, 0, 0, 0.5);
  position: absolute;
  width: 100%;
  min-height: 600px;
  height: 100vh;
  margin-top: 0;
  padding-top: 0;
}

.slider {
  background-image: url(../img/imagesContent/hero.webp);
  min-height: 600px;
  height: 100vh;
  background-size: cover;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
}
.slider h1 {
  z-index: 3;
  font-size: 4rem;
}
@media (min-width: 768px) {
  .slider h1 {
    font-size: 6rem;
  }
}
.slider em {
  font-size: 2rem;
  font-family: "Dosis", sans-serif;
  font-weight: bold;
}
@media (min-width: 768px) {
  .slider em {
    font-size: 3rem;
  }
}

.div-ofrecer {
  margin: 8rem auto;
}
@media (min-width: 768px) {
  .div-ofrecer {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
.div-ofrecer .div-imagen {
  max-width: 10rem;
  margin: 0 auto;
}
.div-ofrecer p, .div-ofrecer h2 {
  color: #3d3a3a;
  font-size: 2rem;
  line-height: 2.5rem;
  text-align: center;
  font-weight: bold;
}
.div-ofrecer h2 {
  font-size: 3rem;
}

.descuento-target {
  background-image: url(/img/imagesContent/descuento.webp);
  width: 100%;
  min-height: 60rem;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.descuento-target h1 {
  font-size: 3rem;
}
.descuento-target p {
  font-size: 2rem;
  font-weight: bold;
}
.descuento-target p, .descuento-target h1 {
  z-index: 4;
}
.descuento-target .cortina2 {
  min-height: 60rem;
  height: 90vh;
}